@import url(https://fonts.googleapis.com/css?family=Be+Vietnam:300i,400,600i&display=swap);
/* =================VARIABLES================= */
/* =================GLOBAL RESET================= */
*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  box-sizing: border-box;
  font-family: 'Be Vietnam', sans-serif;
  line-height: 1;
  color: #4d4d4d;
  font-weight: 300;
  font-size: 2rem;
  background-color: #fafafa;
  padding: 0px 10vh; }

ul {
  margin: 0;
  padding: 0; }
  ul li {
    list-style-type: none; }

/* =================MIXINS================= */
/* =================MAIN PAGE================= */
.page {
  display: flex; }

/* =================SIDEBAR================= */
.sidebar {
  width: 35vh;
  height: 100vh;
  padding-top: 7rem;
  border-right: 1px solid #f1f1f1;
  background-color: #fafafa; }
  .sidebar li {
    padding: 1rem;
    list-style-type: none;
    display: flex;
    cursor: pointer;
    line-height: 2rem; }
    .sidebar li:hover .sidebar__list-delete {
      display: inline-block;
      margin-right: 0; }
    .sidebar li:hover, .sidebar li.active {
      background-color: white;
      font-weight: bold; }
  .sidebar__list {
    padding: 1rem .5rem;
    display: flex;
    justify-content: space-between; }
    .sidebar__list-delete {
      display: none; }
      .sidebar__list-delete.active {
        display: inline-block;
        margin-right: 0; }
      .sidebar__list-delete svg {
        color: #b4b4b4; }
  .sidebar .sidebar__list-dropdown {
    display: flex;
    align-content: center;
    padding-left: .5rem;
    cursor: pointer;
    padding-bottom: 2rem;
    border-bottom: 1px solid #f1f1f1; }
    .sidebar .sidebar__list-dropdown span {
      display: flex;
      padding-right: 1rem; }
      .sidebar .sidebar__list-dropdown span svg {
        align-self: flex-end;
        -webkit-transition: -webkit-transform .5s;
        transition: -webkit-transform .5s;
        transition: transform .5s;
        transition: transform .5s, -webkit-transform .5s; }
        .sidebar .sidebar__list-dropdown span svg.hide-lists {
          -webkit-transform: rotate(-90deg);
                  transform: rotate(-90deg); }
    .sidebar .sidebar__list-dropdown h2 {
      color: #50C878;
      font-weight: bold; }

.addList {
  padding: 0 1rem;
  margin-top: 2rem;
  cursor: pointer; }
  .addList__content {
    margin-bottom: 2rem; }
  .addList__input {
    width: 100%;
    border: 1px solid #f1f1f1;
    border-radius: .3rem;
    height: 3rem;
    padding: .5rem 1rem; }
  .addList__text {
    display: inline-block;
    color: gray;
    font-size: 2rem;
    margin-left: 1rem; }
  .addList__icon {
    color: #50C878;
    font-size: 1.5rem; }
  .addList__button {
    width: 7rem;
    background-color: #50C878;
    border: 1px solid transparent;
    color: white;
    margin: 1rem .5rem;
    font-weight: bold;
    font-size: 1.4rem;
    line-height: 1.5rem;
    padding: .5rem 1rem;
    position: relative;
    display: inline-block;
    border-radius: .3rem;
    text-decoration: none;
    text-align: center;
    cursor: pointer; }
  .addList__cancel {
    color: #555;
    font-size: 1.5rem;
    margin: .2rem .5rem;
    font-weight: normal;
    cursor: pointer; }

.delete-modal {
  position: relative; }
  .delete-modal__content {
    box-shadow: 0 3px 2px #777;
    position: absolute;
    width: 100%;
    border-radius: .3rem;
    z-index: 100;
    width: 15vw;
    padding: .5rem;
    top: 1rem;
    right: 0;
    background-color: white; }
    .delete-modal__content p {
      margin-bottom: .5rem;
      line-height: normal;
      font-weight: normal;
      font-size: 1.5rem; }
    .delete-modal__content button {
      width: 7rem;
      background-color: #50C878;
      border: 1px solid transparent;
      color: white;
      margin: 1rem .5rem;
      font-weight: bold;
      font-size: 1.4rem;
      line-height: 1.5rem;
      padding: .5rem 1rem;
      position: relative;
      display: inline-block;
      border-radius: .3rem;
      text-decoration: none;
      text-align: center;
      cursor: pointer; }
    .delete-modal__content span {
      color: #555;
      font-size: 1.5rem;
      margin: .2rem .5rem;
      font-weight: normal;
      cursor: pointer; }

/* =================TASKS================= */
.tasks {
  width: 60vw;
  height: 100vh;
  background-color: white;
  border-right: 1px solid #f1f1f1;
  padding: 4rem; }
  .tasks__filter-current, .tasks__filter-done {
    width: 8rem;
    background-color: #50C878;
    border: 1px solid transparent;
    color: white;
    margin: 1rem .5rem;
    font-weight: bold;
    font-size: 1.4rem;
    line-height: 1.5rem;
    padding: .5rem 1rem;
    position: relative;
    display: inline-block;
    border-radius: .3rem;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    margin: 0;
    border-radius: 0px;
    background-color: transparent;
    color: #50C878;
    border: 1px solid #50C878;
    margin-bottom: 2rem; }
    .tasks__filter-current.active, .tasks__filter-done.active {
      background-color: #50C878;
      color: white; }
    .tasks__filter-current:focus, .tasks__filter-done:focus {
      outline: none; }
  .tasks__filter-delete, .tasks__filter-restore {
    width: 8rem;
    background-color: #50C878;
    border: 1px solid transparent;
    color: white;
    margin: 1rem .5rem;
    font-weight: bold;
    font-size: 1.4rem;
    line-height: 1.5rem;
    padding: .5rem 1rem;
    position: relative;
    display: inline-block;
    border-radius: .3rem;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    margin-top: 5rem; }
  .tasks .list-name {
    font-size: 3rem;
    font-weight: normal;
    margin-bottom: 2rem; }
  .tasks__list-done {
    text-decoration: line-through;
    opacity: .5; }
  .tasks__list li {
    display: flex;
    line-height: 2rem;
    font-size: 1.6rem;
    padding: .5rem;
    border-bottom: 1px solid #f1f1f1; }
  .tasks .checkbox-div {
    margin-right: 2rem;
    display: flex;
    justify-content: center;
    align-items: center; }
    .tasks .checkbox-div .checkbox {
      cursor: pointer;
      height: 1.6rem;
      width: 1.6rem; }

